"use client";

import { useState, useEffect } from "react";
import { FiSliders, FiShoppingCart } from "react-icons/fi";

// UI Components
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from "@/components/ui/select";
import {
  Pagination,
  PaginationContent,
  PaginationEllipsis,
  PaginationItem,
  PaginationLink,
  PaginationNext,
  PaginationPrevious,
} from "@/components/ui/pagination";

// Components
import ProductCard from "@/components/common/ProductCard";
import BreadcrumbShop from "@/components/catalogpage/BreadcrumbShop";
import MobileFilters from "@/components/catalogpage/filters/MobileFilters";
import Filters from "@/components/catalogpage/filters";

// Data
import {
  shopProductsData,
  marketProductsData,
  relatedProductData,
  topSellingData,
} from "@/lib/data";

// Import de la police Montserrat depuis Google Fonts
import { Montserrat } from "next/font/google";

// Configuration de la police Montserrat
const montserrat = Montserrat({
  subsets: ["latin"],
  weight: "800", // ExtraBold
  display: "swap",
});

// Interface pour typage des produits
interface Product {
  id: number;
  name: string;
  price: number;
  image: string;
  category?: string;
  rating?: number;
  discountPrice?: number;
  // Ajoutez d'autres champs selon vos donnes relles
}

// Donnes des publicits avec images corriges
const ads = [
  {
    title: "Qualit et lgance  prix mini !",
    description:
      "Des pices tendance, un style raffin et des prix qui font plaisir !  Trouvez la tenue parfaite qui vous ressemble, sans compromis sur la qualit.  Faites-vous plaisir ds aujourdhui",
    image: "https://images.unsplash.com/photo-1551488831-00ddcb6c6bd3?q=80&w=1470&auto=format&fit=crop", // Remplacement de /images/a.png
    bgColor: "bg-gradient-to-r from-indigo-500 to-purple-600",
  },
  {
    title: "Faites un pas vers le style",
    description: "Marchez avec lgance. Dcouvrez notre collection de chaussures tendances et confortables.",
    image: "https://images.unsplash.com/photo-1542291026-7eec264c27ff?q=80&w=1470&auto=format&fit=crop",
    bgColor: "bg-gradient-to-r from-red-600 to-rose-600",
  },
  {
    title: "Profitez de remises jusqu' 50% sur nos collections !",
    description: "Mode, style et conomies runis en un clic ! Dpchez-vous, stock limit.",
    image: "https://images.unsplash.com/photo-1558769132-cb1aea458c5e?q=80&w=1470&auto=format&fit=crop",
    bgColor: "bg-gradient-to-r from-red-500 to-orange-500",
  },
  {
    title: "Craquez pour les meilleures offres !",
    description: "Ne ratez pas l'occasion de renouveler votre garde-robe avec style et  petit prix.  Offre limite, faites vite !",
    image: "https://images.unsplash.com/photo-1556228453-efd6c1ff04f6?q=80&w=1470&auto=format&fit=crop",
    bgColor: "bg-gradient-to-r from-pink-500 to-rose-500",
  },
  {
    title: "Profitez de la diversit des marques ! ",
    description: "Dcouvrez un large choix de marques tendance  prix imbattables ! Que vous cherchiez du chic, du casual ou du streetwear, trouvez votre style parfait en un clic.",
    image: "https://images.unsplash.com/photo-1551488831-00ddcb6c6bd3?q=80&w=1470&auto=format&fit=crop",
    bgColor: "bg-gradient-to-r from-green-400 to-lime-500",
  },
];

export default function CatalogPage() {
  const [currentAd, setCurrentAd] = useState(0);
  const [currentPage, setCurrentPage] = useState(1);
  const productsPerPage = 12;

  // Fusion des donnes des produits
  const allProducts: Product[] = [
    ...shopProductsData,
    ...marketProductsData,
    ...relatedProductData,
    ...topSellingData,
  ].slice(0, 100); // Limite  100 produits pour correspondre au texte "Affichage de 1  12 sur 100 produits"

  // Calcul de la pagination
  const totalPages = Math.ceil(allProducts.length / productsPerPage);
  const paginatedProducts = allProducts.slice(
    (currentPage - 1) * productsPerPage,
    currentPage * productsPerPage
  );

  // Gestion du carrousel publicitaire
  useEffect(() => {
    const interval = setInterval(() => {
      setCurrentAd((prev) => (prev === ads.length - 1 ? 0 : prev + 1));
    }, 5000);
    return () => clearInterval(interval);
  }, []);

  const handleDotClick = (index: number) => {
    setCurrentAd(index);
  };

  const handlePageChange = (page: number) => {
    setCurrentPage(page);
    window.scrollTo({ top: 0, behavior: "smooth" }); // Scroll en haut de la page
  };

  return (
    <main className="pb-20">
      <div className="max-w-frame mx-auto px-4 xl:px-0">
        <hr className="h-[1px] border-t-black/10 mb-5 sm:mb-6" />
        <BreadcrumbShop />
        <div className="mb-8 flex flex-col space-y-4">
          <div className="flex items-center justify-between">
            <div className="flex items-center space-x-2">
              {/* Titre Marketplace avec la police Montserrat */}
              <h1
                className={`text-2xl md:text-[32px] font-extrabold uppercase tracking-tight ${montserrat.className}`}
                style={{ fontFamily: "'Montserrat', sans-serif" }}
              >
                Marketplace
              </h1>
              <FiShoppingCart className="text-black text-2xl md:text-3xl" />
            </div>
            <MobileFilters />
          </div>
          <div className="flex flex-col sm:flex-row sm:items-center space-y-2 sm:space-y-0 sm:space-x-4">
            <span className="text-sm md:text-base text-black/60">
              Affichage de {(currentPage - 1) * productsPerPage + 1} {" "}
              {Math.min(currentPage * productsPerPage, allProducts.length)} sur{" "}
              {allProducts.length} produits
            </span>
            <div className="flex items-center">
              Trier par:{" "}
              <Select defaultValue="most-popular">
                <SelectTrigger className="font-medium text-sm px-1.5 sm:text-base w-fit text-black bg-transparent shadow-none border-none">
                  <SelectValue />
                </SelectTrigger>
                <SelectContent>
                  <SelectItem value="most-popular">Le plus populaire</SelectItem>
                  <SelectItem value="low-price">Prix bas</SelectItem>
                  <SelectItem value="high-price">Prix lev</SelectItem>
                </SelectContent>
              </Select>
            </div>
          </div>
        </div>
        <div className={`${ads[currentAd].bgColor} rounded-2xl shadow-lg overflow-hidden mb-8 text-white`}>
          <div className="flex flex-col sm:flex-row items-center justify-between h-72 sm:h-56 md:h-64">
            <div className="w-full sm:w-1/2 p-6 flex flex-col justify-center">
              <h2 className="text-2xl md:text-3xl font-bold">{ads[currentAd].title}</h2>
              <p className="text-sm md:text-base mt-2">{ads[currentAd].description}</p>
            </div>
            <div className="w-full sm:w-1/2 h-full">
              <img
                src={ads[currentAd].image}
                alt={ads[currentAd].title}
                className="w-full h-full object-cover"
              />
            </div>
          </div>
          <div className="flex justify-center gap-2 py-4 bg-black/20">
            {ads.map((_, index) => (
              <button
                key={index}
                onClick={() => handleDotClick(index)}
                className={`h-2 w-2 rounded-full transition-all duration-300 ${
                  currentAd === index ? "bg-white w-4" : "bg-gray-300"
                }`}
              />
            ))}
          </div>
        </div>
        <div className="flex md:space-x-5 items-start">
          <div className="hidden md:block min-w-[295px] max-w-[295px] border border-black/10 rounded-[20px] px-5 md:px-6 py-5 space-y-5 md:space-y-6">
            <div className="flex items-center justify-between">
              <span className="font-bold text-black text-xl">Catgories</span>
              <FiSliders className="text-2xl text-black/40" />
            </div>
            <Filters />
          </div>
          <div className="flex flex-col w-full space-y-5">
            <div className="w-full grid grid-cols-1 xs:grid-cols-2 sm:grid-cols-3 md:grid-cols-2 lg:grid-cols-3 gap-4 lg:gap-5">
              {paginatedProducts.length > 0 ? (
                paginatedProducts.map((product) => (
                  <ProductCard key={product.id} data={product} />
                ))
              ) : (
                <div className="col-span-full text-center py-12">
                  <p className="text-lg font-semibold text-gray-800">
                    Aucun produit trouv
                  </p>
                  <p className="text-sm text-gray-600 mt-2">
                    Essayez de modifier vos filtres ou votre recherche.
                  </p>
                </div>
              )}
            </div>
            <hr className="border-t-black/10" />
            <Pagination className="justify-between">
              <PaginationPrevious
                onClick={() => handlePageChange(Math.max(1, currentPage - 1))}
                className={`border border-black/10 ${
                  currentPage === 1 ? "opacity-50 cursor-not-allowed" : ""
                }`}
              />
              <PaginationContent>
                {Array.from({ length: totalPages }, (_, i) => i + 1)
                  .slice(
                    Math.max(0, currentPage - 3),
                    Math.min(totalPages, currentPage + 2)
                  )
                  .map((page) => (
                    <PaginationItem key={page}>
                      <PaginationLink
                        onClick={() => handlePageChange(page)}
                        className={`text-black/50 font-medium text-sm ${
                          currentPage === page ? "text-black font-bold" : ""
                        }`}
                        isActive={currentPage === page}
                      >
                        {page}
                      </PaginationLink>
                    </PaginationItem>
                  ))}
                {totalPages > 5 && currentPage < totalPages - 2 && (
                  <PaginationItem>
                    <PaginationEllipsis className="text-black/50 font-medium text-sm" />
                  </PaginationItem>
                )}
              </PaginationContent>
              <PaginationNext
                onClick={() =>
                  handlePageChange(Math.min(totalPages, currentPage + 1))
                }
                className={`border border-black/10 ${
                  currentPage === totalPages ? "opacity-50 cursor-not-allowed" : ""
                }`}
              />
            </Pagination>
          </div>
        </div>
      </div>
    </main>
  );
}